/**
 * Verify 验证码组件
 * @description 分发验证码使用
 * */
// import VerifyCode from './Verify/VerifyCode'
import VerifySlide from './verifySlider/verifySlider'
import VerifyPoint from "./verifyPoint/verifyPoint"


export default {
    name: 'Vue2Verify',
    props: {
        containerId:{
            type:String,
            // required:true
        },
        captchaType:{
            type:String,
            required:true
        },
        type: {
            type: String | Number,
            require: false,
            // default: 'picture' //数字图片码
        },
        figure: {
            type: Number
        },
        arith: {
            type: Number
        },
        width: {
            type: String,
            default:"310px"
        },
        height: {
            type: String
        },
        fontSize: {
            type: String
        },
        codeLength: {
            type: Number
        },
        mode: {
            type: String,
            default:'pop'
        },
        vOffset: {
            type: Number
        },
        vSpace: {
            type: Number,
            default:5
        },
        explain: {
            type: String,
            default: '向右滑动完成验证'
        },
        imgSize: {
            type: Object,
            default() {
                return {
                    width: '310px',
                    height: '155px'
                }
            }
        },
        blockSize: {
            type: Object,
            default() {
                return {
                    width: '50px',
                    height: '50px'
                }
            }
        },
        barSize: {
            type: Object
        },
        //默认的文字数量
        defaultNum: {
            type: Number
        },
        //校对的文字数量
        checkNum: {
            type: Number,
            default: 3
        },
        showButton: {
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            // showBox:true,
            clickShow:false,
            // 内部类型
            verifyType: undefined,
            // 所用组件类型
            componentType: undefined
        }
    },
    methods: {
        /**
         * checkCode
         * @description 判断验证码
         * */
        checkCode() {
            if (this.instance.checkCode) {
                this.instance.checkCode()
            }
        },
        /**
         * refresh
         * @description 刷新
         * */
        refresh() {
            if (this.instance.refresh) {
                this.instance.refresh()
            }
        },
        show(){
            if (this.mode=="pop") {
                this.clickShow = true;
            }
        }
    },
    computed: {
        instance() {
            return this.$refs.instance || {}
        },
        showBox(){
            if (this.mode=='pop') {
                return this.clickShow
            }else{
                return true;
            }
        }
    },
    watch: {
        type: {
            immediate: true,
            handler(type) {
                if (type) {
                    switch (type.toString()) {
                        case 'picture':
                            this.verifyType = '1'
                            this.componentType = 'VerifyCode'
                            break
                        case '1':
                            this.verifyType = '1'
                            this.componentType = 'VerifyCode'
                            break
                        case 'compute':
                            this.verifyType = '2'
                            this.componentType = 'VerifyCode'
                            break
                        case '2':
                            this.verifyType = '2'
                            this.componentType = 'VerifyCode'
                            break
                        case 'slide':
                            this.verifyType = '1'
                            this.componentType = 'VerifySlide'
                            break
                        case '3':
                            this.verifyType = '1'
                            this.componentType = 'VerifySlide'
                            break
                        case 'puzzle':
                            this.verifyType = '2'
                            this.componentType = 'VerifySlide'
                            break
                        case '4':
                            this.verifyType = '2'
                            this.componentType = 'VerifySlide'
                            break
                        case 'pick':
                            this.verifyType = ''
                            this.componentType = 'VerifyPoints'
                            break
                        case '5':
                            this.verifyType = ''
                            this.componentType = 'VerifyPoints'
                            break
                        default:
                            this.verifyType = undefined
                            this.componentType = undefined
                            console.error('Unsupported Type:' + type)
                    }
                }
            }
        },
        captchaType:{
            immediate: true,
            handler(captchaType) {
                switch (captchaType.toString()) {
                    case 'blockPuzzle':
                        this.verifyType = '2'
                        this.componentType = 'VerifySlide'
                        break
                    case 'clickWord':
                        this.verifyType = ''
                        this.componentType = 'VerifyPoints'
                        break
                }
            }
        },
    },
    components: {
        VerifySlide,
        VerifyPoint
    },
}
